<template>
    <view class="container">

        <view :class="{ active: isActive }">111</view>
        <view class="static" :class="{activeClass, errorClass}">333</view>
        <picker mode="date" :value="picker_date" start="2015-09-01" end="2017-10-01" @change="bindDateChange">
            <view class="picker">
                当前选择: {{ picker_date }}
            </view>
        </picker>
        <view class="">{{ h5 }} </view>
        
    </view>
</template>

<script>
// import num from '@/common/mixin/mixin'

export default {
    // mixins:[ num.num2 ],
    data() {
        return {
            isActive: true,
            activeClass: 'activeClass',
            errorClass: 'errorClass',
            picker_date: '',
            h5: ''
        };
    },
    methods: {
		bindDateChange(e){
			console.log(e);
            this.picker_date = e.detail.value                         
        },
    },
    onLoad(){
        console.log('onLoad');
        console.log(this);

        this.h5 = 10; 
        //#ifdef H5
            this.h5 = 'h5'
            console.log('这是h5');
        //#endif

        //#ifdef WP-WEIXIN
            this.h5 = 'weixin'
            console.log('这是weixin');
        //#endif 
    }
};
</script>

<style>
.container {
    padding: 20px;
    font-size: 14px;
    line-height: 24px;
}
.active {
    color: blue;
}
.container .activeClass {
    background: rgb(32, 32, 32);
}
.container .errorClass {
    color: #fff;
}
</style>
